/* welcome page styling  */
.welcome{
    background-color: #ebe8e5;
    background-size: cover;
    margin-top: -20px;
    height: 30cm;
    padding-left: 4%;
    padding-right: 4%;
}
/* 
@media only screen and (max-width: 950px){
   .welcome{
    height: 35cm;
   } 
} */

.welcome >div{
    padding-top:30px;
    margin-left: 8%;
    margin-right: 8%;
    
}

/* header of the .welcome and the rooms section */
.rooms>div>h1, .welcome > div >h1, .photos> div> h1, .menu>div>h1,  .eventsHeader>h1{
    font-family:cursive;
    font-size: 50px;
    padding-top:40px;
    
    
}

.welcome>div>p, .rooms>div>p, .photos>div>p, .eventsHeader>p{
    font-size: large;
    padding-top: 15px ;
    color: #545b61;
    padding: 10px;
    font-weight: 300;
    font-family: cursive;
}



/* button inside the welcome page styling */
.welcome-buttons{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 30px;
  
}
/* the or word between buttons */
.welcome-buttons > p{
    font-style: italic;
    font-size: 2rem;
    margin-left: 15px;
    margin-right: 15px;
    padding-top: 10px;
    display: flex;
    flex-wrap: wrap;
   
}
/* both butttons */
.welcome-buttons >a{
    color: white;
    border-radius: 40px;
    box-sizing: border-box;
    border-width:10px; 
    display: block;
    height: 80px;
    font-size: 1.7em;
    padding: 4px;
    margin-top: 30px;
    margin-bottom: 30px;
    text-decoration: none;
    width: 10em;
    z-index: 2;
    border: 2px solid #ffba5a;
}

/* the text inside the buttoms */
.welcome-buttons>a>span{
  margin:auto;
  padding:auto;
  align-items: center;
  background: #0e0e10;
  border-radius: 40px;
  display: flex;
  justify-content: center;
  height: 100%;
  transition: background 0.5s ease;
  background:transparent;
  width: 100%;
  font-style: italic;
 

}

.welcome-buttons>a:hover{
    color:  #0e0e10;
    font-weight: 600;
    background-image: linear-gradient(to right, #808080 0%, #ffba5a 100%);
    border: transparent;
}

/* the first button  */
.welcome-buttons >a:nth-child(1){
    margin-left: 40px;
    background-color: #ffba5a;
    
}

.welcome-buttons>a:last-child{
    float:right;
    color: #fd8421;
    margin-right: 40px
    
}
/* the last button when hoverd */
.welcome-buttons>a:last-child:hover{
    color: #0e0e10;
}



/* photo college inside welcome page */
.overlay-pic{
    position:absolute;
    left:65%;
    top: 1400px;
    border-radius: 50%;
    height: 50%;
    border: 20px  solid #ebe8e5;
    
}

.mainPic{
    box-shadow: 3px 5px 17px #7a848c;
}

@media only screen and (max-width: 750px){
    .welcome-buttons >a{
        height: 60px;
        font-size: 1.5em;
        padding: 2px;
        margin-top: 20px;
        margin-bottom: 10px;
    }
 .mainPic{
        height:40%;
        width:80%;
        margin-right:50px;
        margin-bottom:50px;
    }
    .or{
        width:100%;
        justify-content: center;
    }
    .overlay-pic{
        top:1130px;
        height: 30%;
        left: 37%;
    }

    .welcome-buttons >a:nth-child(1){
        margin-left: 10px;
        margin-bottom: -20px;
    }

    .welcome-buttons >a:nth-child(3){
        margin-left: 20px;
        margin-top: -20px;
        
    }

    .welcome-buttons>a{
        height: 60px;
    }

    .welcome>div>p, .rooms>div>p, .photos>div>p, .eventsHeader>p{
        font-size: large;
        padding-top: 1px ;
        color: #545b61;
        padding: 1px;
        font-weight: 200;
        font-family: cursive;
    }
    .rooms>div>h1, .welcome > div >h1, .photos> div> h1, .menu>div>h1,  .eventsHeader>h1{
        font-family:cursive;
        font-size: 35px;
        padding-top:20px;
        
        
    }
    .welcome{
        height: 23cm;
    }
}

/* footer styling */

.footer{
    background-color:#0e0e10;
    color:#fff;
    padding:30px;
    font-size: 1.2rem;
    
}

.footer>div>img{
    height: 50px;
    border-radius: 50%;
}

.footer>div>a{
    display: block;
    margin: 15px;
    color:#818c95;
    text-decoration: none;
}

.footer>div>a:hover{
  
    color:#e8a952
    
}

.icons{
    display: flex;
    justify-content: center;
}

.topIcon{
    color:#e8a952;
    margin-top: 30px;
}

.restIcon{
    color:#e8a952;
}
/* rooms Page styling */
.rooms{
    position: relative;
    
}

/* the description inside the rooms div */
.rooms>div>p{
    width: 60%;
    margin-left: 20%;
    margin-right: 20%;
  
}

.room-card{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    
 
}

/* the rooms card picture */
.room-card img{
    height: 6cm;
    width: auto;
    margin: 10px ;
    box-shadow: 1px 3px 11px #545b61;
    border-radius: 2%;
}

/* room pic effecton hover */
.room-card img:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
/* the room type name */
.roomCard> header{
    color: #e8a952;
    font-size: 1.5rem;
    padding: 10px;
   
}


/* the room pricings */
.roomCard> footer{
    font-size: 1rem;
    font-family: monospace;
    font-weight: 300;
}
